/* сцена секции */
.story {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background: #f8f5ed;
  color: #6e6d6b;
  font-family: "Poppins", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;
  overflow: hidden;
  isolation: isolate;
}

/* канвас с частицами */
.particle-canvas {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

/* внутренний контейнер */
.story-inner {
  position: relative;
  z-index: 2;
  max-width: 900px;
  width: 100%;
  padding: 0 16px;
  display: flex;
  justify-content: center;
}

/* карточка текста */
.story-card {
  background: radial-gradient(
      circle at 10% 0%,
      rgba(53, 52, 51, 0.07) 0%,
      rgba(6, 6, 6, 0.16) 70%
    ),
    rgba(32, 28, 24, 0.6);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 230, 190, 0.18);
  box-shadow: 0 60px 140px rgba(0, 0, 0, 0.9),
    0 0 120px rgba(255, 210, 150, 0.15);
  border-radius: 28px;
  padding: 40px 28px 48px;
  max-width: 680px;
  text-align: center;
  position: relative;
  isolation: isolate;
  overflow: hidden;
}

/* лёгкое внутреннее свечение карточки */
.story-card::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at 50% 0%,
    rgba(108, 107, 105, 0.25) 0%,
    rgba(0, 0, 0, 0) 70%
  );
  mix-blend-mode: screen;
  opacity: 0.4;
  pointer-events: none;
}

/* заголовок */
.story-heading {
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  font-weight: 400;
  font-size: clamp(1.7rem, 1rem + 1vw, 2.2rem);
  line-height: 1.35;
  color: #000000;
  letter-spacing: -0.02em;
  margin: 0 auto 24px;
  max-width: 18ch;
  text-wrap: balance;
  position: relative;
  background: radial-gradient(
    circle at 20% 20%,
    #000000 0%,
    #000000 30%,
    #000000 50%,
    #000000 60%
  );
  -webkit-background-clip: text;
  color: transparent;
  text-shadow: 0 30px 60px rgba(0, 0, 0, 0.9);
  animation: headingFade 1.4s cubic-bezier(0.16, 1, 0.3, 1) forwards;
  opacity: 0;
}

.heading-glow {
  display: block;
  font-size: 0.9em;
  font-weight: 400;
  background: radial-gradient(
    circle at 20% 20%,
    #000000 0%,
    #252523 40%,
    rgba(17, 17, 17, 0.5) 70%
  );
  -webkit-background-clip: text;
  color: transparent;
  filter: drop-shadow(0 12px 24px rgba(133, 131, 129, 0.4));
}

/* основной текст */
.story-text {
  color: #000000;
  font-size: 1rem;
  line-height: 1.7;
  font-weight: 300;
  max-width: 46ch;
  margin: 0 auto 36px;
  opacity: 0;
  transform: translateY(20px);
  animation: textFadeUp 1.2s ease forwards 0.4s;
  text-wrap: pretty;
}

/* блок приглашения */
.invite-block {
  display: flex;
  justify-content: center;
  margin-bottom: 32px;
}

.invite-frame {
  position: relative;
  border-radius: 20px;
  padding: 28px 24px 26px;
  background: rgba(0, 0, 0, 0.2);
  background-image: radial-gradient(
      circle at 20% 20%,
      rgba(255, 226, 180, 0.08) 0%,
      rgba(171, 168, 168, 0) 70%
    ),
    radial-gradient(
      circle at 80% 80%,
      rgba(255, 200, 150, 0.05) 0%,
      rgba(0, 0, 0, 0) 70%
    );
  border: 1px solid rgba(255, 210, 150, 0.22);
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.8),
    0 0 80px rgba(255, 200, 140, 0.2);
  min-width: min(280px, 90%);
  max-width: 360px;

  opacity: 0;
  transform: scale(0.96) translateY(20px);
  animation: cardPop 1s cubic-bezier(0.16, 1, 0.3, 1) forwards 0.8s;
}

/* декоративные "золотые уголки" */
.frame-corner {
  position: absolute;
  width: 26px;
  height: 26px;
  border: 1px solid rgba(155, 155, 155, 0.5);
  filter: drop-shadow(0 0 8px rgba(189, 186, 183, 0.6));
}

.frame-top-left {
  top: 10px;
  left: 10px;
  border-bottom: 0;
  border-right: 0;
  border-radius: 8px 0 0 0;
}
.frame-top-right {
  top: 10px;
  right: 10px;
  border-bottom: 0;
  border-left: 0;
  border-radius: 0 8px 0 0;
}
.frame-bottom-left {
  bottom: 10px;
  left: 10px;
  border-top: 0;
  border-right: 0;
  border-radius: 0 0 0 8px;
}
.frame-bottom-right {
  bottom: 10px;
  right: 10px;
  border-top: 0;
  border-left: 0;
  border-radius: 0 0 8px 0;
}

.invite-content {
  position: relative;
  text-align: center;
  z-index: 2;
}

.invite-title {
  font-family: "Playfair Display", Georgia, serif;
  color: #fff4e2;
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 1.4;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  margin: 0 0 12px;
  text-shadow: 0 20px 40px rgba(0, 0, 0, 0.8);
}

.invite-details {
  margin: 0;
  font-size: 0.95rem;
  font-weight: 300;
  line-height: 1.5;
  color: #ffe7c2;
  text-shadow: 0 24px 50px rgba(0, 0, 0, 0.9);
}

.accent {
  font-weight: 400;
  color: #fff;
  background: radial-gradient(
    circle at 20% 20%,
    #ffffff 0%,
    #ffe5bd 40%,
    rgba(255, 221, 170, 0.5) 70%
  );
  -webkit-background-clip: text;
  color: transparent;
  white-space: nowrap;
}

.light {
  color: rgba(255, 231, 194, 0.8);
  font-style: italic;
  font-weight: 300;
}

/* нижняя подпись */
.meta-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 12px;
  row-gap: 12px;
  color: rgba(255, 231, 194, 0.6);
  font-size: 0.7rem;
  font-weight: 400;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  margin-top: 12px;
  opacity: 0;
  animation: fadeInSoft 1.2s ease forwards 1.4s;
}

.meta-pill {
  font-family: "Poppins", sans-serif;
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid rgba(255, 231, 194, 0.25);
  background: rgba(255, 231, 194, 0.03);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.9),
    0 0 40px rgba(255, 210, 150, 0.15);
  line-height: 1;
}

.dot-sep {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  align-self: center;
  background: rgba(255, 231, 194, 0.4);
  box-shadow: 0 0 10px rgba(255, 210, 150, 0.8);
}

/* анимации появления */
@keyframes headingFade {
  0% {
    opacity: 0;
    transform: translateY(20px) scale(0.98);
    filter: blur(4px);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
    filter: blur(0);
  }
}

@keyframes textFadeUp {
  0% {
    opacity: 0;
    transform: translateY(20px) scale(0.99);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes cardPop {
  0% {
    opacity: 0;
    transform: scale(0.9) translateY(30px);
    filter: blur(6px);
  }
  60% {
    opacity: 1;
    transform: scale(1.02) translateY(0);
    filter: blur(0);
  }
  100% {
    transform: scale(1) translateY(0);
  }
}

@keyframes fadeInSoft {
  0% {
    opacity: 0;
    filter: blur(3px);
  }
  100% {
    opacity: 1;
    filter: blur(0);
  }
}

/* адаптив */
@media (max-width: 768px) {
  .story {
    padding: 60px 16px;
  }

  .story-card {
    padding: 32px 20px 36px;
    border-radius: 22px;
  }

  .story-heading {
    font-size: 1.6rem;
  }

  .story-text {
    font-size: 0.95rem;
    line-height: 1.6;
  }

  .invite-title {
    font-size: 1rem;
  }

  .invite-details {
    font-size: 0.9rem;
  }

  .meta-row {
    font-size: 0.6rem;
    row-gap: 10px;
  }
}

@media (max-width: 420px) {
  .story-heading {
    font-size: 1.4rem;
  }

  .story-text {
    font-size: 0.9rem;
  }
}
